import classNames from "classnames";
import { ICurrency } from "lib/api/currencies";
import {HTMLAttributes, useCallback, useState} from "react";
import { NpDropdownArrowIcon } from "ui";
import { Icon } from "ui/icon";

import "./currency-select.scss";

interface ICurrencySelectComponentProps extends HTMLAttributes<HTMLDivElement> {
  currency: ICurrency;
  disabled: boolean;
  options?: ICurrency[];
  onCurrencySelectClick: (currencyName: string) => void;
}

export function CurrencySelect({
  currency,
  options,
  className,
  disabled,
  onCurrencySelectClick,
  ...rest
}: ICurrencySelectComponentProps): JSX.Element {
    const [isOpen, setOpen] = useState(false);

    const isMobile = () => Boolean(navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/iPhone/i));

    const toggleOpen = useCallback(
        () => {
            setOpen((prev) => !prev);
        },
        [],
    );

    const chooseOption = useCallback(
        (value: string) => {
            toggleOpen();
            onCurrencySelectClick(value);
        }, [],
    )
  return (
        <div
          className={classNames(
            "CurrencySelect",
            disabled ? "CurrencySelect--disabled" : "",
            className
          )}
          {...rest}
        >
            { isMobile()
                ? <>
                    <Icon className={"CurrencySelect__icon"}>
                        <img src={currency.logoUrl} alt={currency.viewedTicker} />
                    </Icon>
                    <select className={"CurrencySelect__select"}
                            onChange={(event) => onCurrencySelectClick(event.target.value)}
                    >
                        {
                            options?.map(option => (
                                <option key={option.ticker} value={option.ticker}>{option.ticker}</option>
                            ))
                        }

                    </select>
                </>
                : <>
                    <div className={"CurrencySelect__container"}>
                        <button  className={"CurrencySelect__button"} onClick={toggleOpen}>
                            <Icon className={"CurrencySelect__icon"}>
                                <img src={currency.logoUrl} alt={currency.viewedTicker} />
                            </Icon>
                            <div className={"CurrencySelect__text"}>{currency.ticker}</div>
                            <Icon size={20} className={"CurrencySelect__arrow"}>
                                <NpDropdownArrowIcon />
                            </Icon>
                        </button>
                        {isOpen && <ul className={"CurrencySelect__list"}>
                            {
                                options?.map(option => (
                                    <li key={option.ticker} className={"CurrencySelect__item"} onClick={() => chooseOption(option.ticker)}>
                                        <Icon className={"CurrencySelect__icon"}>
                                            <img src={option.logoUrl} alt={option.ticker} />
                                        </Icon>
                                        {option.ticker}
                                    </li>
                                ))
                            }
                        </ul>}
                    </div>
                </>}
        </div>
  );
}
