import { useMemo } from "react";
import { useParams } from "react-router";
import useSWR from "swr";

import { environment } from "lib/config";
import { ICurrency } from "lib/api/currencies";
import { currenciesApi, TPayment } from "lib/api";
import { nearToYoktoNear, useNear } from "lib/near";
import { usePayment } from "models/payment";

export const useModel = () => {
  const { id: paymentId } = useParams();
  const currenciesSwr = useSWR("currencies", () => currenciesApi.fetchCurrencies(paymentId));
  const { payment, isExpired, handleExpired } = usePayment();

  const near = useNear();
  const handlerPay = useHandlerPay({ near, payment: payment ?? null });

  const defaultCurrency = useMemo(
    () => currenciesSwr.data?.find((c) => c.ticker === environment.defaultCurrency),
    [currenciesSwr]
  );
  const isLoaded = !!currenciesSwr.data && (!!payment || isExpired) && near.isReady;

  return {
    isExpired,
    isLoaded,
    payment: payment,
    currencies: currenciesSwr.data,
    defaultCurrency,
    handlerPay,
    handleExpired,
  };
};

const basePath = `${window.location.origin}/payment`;

type UsePayParams = {
  near: ReturnType<typeof useNear>;
  payment: TPayment | null;
};

const useHandlerPay = (params: UsePayParams) => {
  const { near, payment } = params;

  if (!payment) {
    return () => {
      throw new Error("Payment is empty");
    };
  }

  if (!near.isAuthorized) {
    return () =>
      near.login({
        successUrl: `${basePath}/${payment.paymentId}/send`,
        failureUrl: `${basePath}/${payment.paymentId}/failure`,
      });
  }

  return (currency: ICurrency) => {
    switch (currency.ticker) {
      case "NEAR":
        const yoktoNearAmount = nearToYoktoNear(+payment.displayAmount);

        if (!yoktoNearAmount || !payment.address) {
          throw new Error("Data is empty");
        }

        near.send(yoktoNearAmount, payment.address, `${basePath}/${payment.paymentId}/pending`);
        break;

      case "USN":
        const usnUnits = +payment.displayAmountUsd * 1e18;

        if (!usnUnits || !payment.address) {
          throw new Error("Data is empty");
        }

        near.ftTransfer("" + usnUnits, "usn", payment.address, `${basePath}/${payment.paymentId}/pending`);
        break;

      default:
        throw new Error("Unsupported currency");
    }
  };
};
