import { isMobileAndTablet } from "lib/userAgent";
import { ICurrency } from "lib/api/currencies";
import { TPayment } from "lib/api";
import { toast } from "lib/toast";

const useTrustWallet = () => {
  const isAvailable = (currency: ICurrency) => currency.ticker === "NEAR" && isMobileAndTablet();

  return {
    isAvailable,
    pay: async (currency: ICurrency, payment: TPayment) => {
      if (isAvailable(currency) === false) {
        toast("Trust Wallet is not support for current platform or currency");
        return;
      }

      const asset = "c397"; // NEAR ticker id
      const base = "https://link.trustwallet.com/send";
      window.location.href = `${base}?asset=${asset}&address=${payment.address}&amount=${payment.displayAmount}`;
    },
  };
};

export default useTrustWallet;
