import { DEFAULT_FUNCTION_CALL_GAS } from "near-api-js";
import { ICurrency } from "lib/api/currencies";
import { TPayment } from "lib/api";
import { toast } from "lib/toast";

const useSenderWallet = () => {
  // @ts-ignore
  const wallet = window.near;

  const ftTransfer = async (amount: string, contractName: string, receiver: string) =>
    wallet.signAndSendTransaction({
      receiverId: contractName,
      actions: [
        {
          methodName: "ft_transfer",
          args: {
            receiver_id: receiver,
            amount: amount,
          },
          gas: DEFAULT_FUNCTION_CALL_GAS,
          deposit: 1,
        },
      ],
    });

  const assertResult = (result: any, payment: TPayment) => {
    if (result.response.error) {
      toast(result.response.error.kind?.ExecutionError ?? "Contract error");
      return;
    }

    const redirect = `${window.location.origin}/payment/${payment.paymentId}/pending`;
    window.location.href = redirect;
  };

  return {
    isAvailable: wallet?.isSender ?? false,
    pay: async (currency: ICurrency, payment: TPayment) => {
      if (wallet == null || wallet.isSender === false) return;

      if (wallet.isSignedIn() === false) {
        // SenderWallet does not care what the contract id will be
        await wallet.requestSignIn({ contractId: "gopaywith.near" });
      }

      switch (currency.ticker) {
        case "NEAR": {
          const result = await wallet.sendMoney({ receiverId: payment.address, amount: payment.amount });
          assertResult(result, payment);
          return;
        }

        case "USN": {
          const usnUnits = (+payment.displayAmountUsd * 1e18).toString();
          const result = await ftTransfer(usnUnits, "usn", payment.address);
          assertResult(result, payment);
          return;
        }

        default:
          toast("This ticker is not supported yet");
          return;
      }
    },
  };
};

export default useSenderWallet;
