import { ICurrency } from 'lib/api/currencies';
import React from 'react';
import NumberFormat, { NumberFormatValues } from 'react-number-format';
import { Input } from 'ui/input';
import { CurrencySelect } from './currency-select';

interface ICurrencyInputComponentProps
  extends React.HTMLAttributes<HTMLElement> {
  onValueChange: (values: NumberFormatValues) => void;
  currency: ICurrency;
  onCurrencySelectClick: (currencyName: string) => void;
  renderAfter?: React.ReactNode;
  value?: string;
  label: React.ReactNode;
  error?: string;
  selectDisabled?: boolean;
  inputDisabled?: boolean;
  isLoading?: boolean;
  currencies?: ICurrency[];
}

export function CurrencyInput({
  onInput,
  value,
  label,
  onValueChange,
  onCurrencySelectClick,
  error,
  selectDisabled,
  inputDisabled,
  currency,
  currencies,
  isLoading = false,
  ...rest
}: ICurrencyInputComponentProps): JSX.Element {
  const prefix = currency.symbol;
  // const suffix = currency.symbol ? '' : '' + currency.ticker.toUpperCase();

  return (
    <Input
      label={label}
      error={error}
      slotAfterInput={
        <CurrencySelect
          currency={currency}
          onCurrencySelectClick={
            selectDisabled
              ? () => {}
              : onCurrencySelectClick
          }
          disabled={!!selectDisabled}
          options={currencies}
        />
      }
      {...rest}
    >
      {isLoading ? (
        <input disabled readOnly/>
      ) : (
        <NumberFormat
          thousandSeparator={false}
          prefix={prefix}
          onValueChange={onValueChange}
          allowLeadingZeros={true}
          decimalScale={currency.roundOff}
          onInput={onInput}
          value={value}
          placeholder="0.00"
          readOnly={inputDisabled}
        />
      )}
    </Input>
  );
}
