import "./pay-widget.scss";

import { block } from "bem-cn";
import { NpLogoIcon } from "ui";
import { ProceedButton } from "components/proceed-button";
import { ICurrency } from "lib/api/currencies";
import { CurrencyInput } from "components/currency-input";
import classNames from "classnames";
import { WidgetContainer } from "ui/widget-container/widget-container";
import { TPayment } from "lib/api";
import { ExpiryTimer } from "components/expiry-timer";
import { QRCodeSVG } from "qrcode.react";
import { useCallback, useState } from "react";
import { snakeToCamel } from "../../helpers/main";

const b = block("pay-with-near-pay-widget");
const bDescription = block("pay-with-near-pay-widget-description");

interface Props {
  payment: TPayment;
  defaultCurrency: ICurrency;
  hasSenderWallet: boolean;
  hasTrustWallet: (currency: ICurrency) => boolean;
  onTrustWallet: (currency: ICurrency) => void;
  onSenderWallet: (currency: ICurrency) => void;
  onPay: (currency: ICurrency) => void;
  onExpired: () => void;
  currencies?: ICurrency[];
}
export const PayWidget: React.FC<Props> = (props) => {
  const { payment, defaultCurrency, onPay, onExpired, currencies } = props;
  const { onSenderWallet, hasSenderWallet, hasTrustWallet, onTrustWallet } = props;
  const [currency, setCurrency] = useState<ICurrency>();

  const changeCurrency = useCallback(
    (currency: string) => {
      const currentCurrency = currencies?.find((item) => item.ticker === currency);
      setCurrency(currentCurrency);
    },
    [currencies]
  );

  return (
    <WidgetContainer className={b()}>
      <div className={b("title")}>{payment.name}</div>
      <div className={b("description")}>{payment.description}</div>
      <div className={bDescription()}>
        <div>
          <div className={bDescription("label")}>Payment: </div>
          <div>{payment.paymentId}</div>
        </div>
        <div>
          <div className={bDescription("label")}>Recipient: </div>
          <div className={bDescription("address")}>{payment.address}</div>
        </div>
      </div>
      <ExpiryTimer expiryAt={payment.expiryAt} onExpired={onExpired} />
      <CurrencyInput
        label={"Amount to Pay"}
        value={payment[snakeToCamel(currency?.parameter || defaultCurrency?.parameter) as keyof TPayment]}
        currency={currency || defaultCurrency}
        onValueChange={() => {}}
        onCurrencySelectClick={changeCurrency}
        selectDisabled={false}
        currencies={currencies}
        className="mb-4"
      />

      <ProceedButton onClick={() => onPay(currency || defaultCurrency)}>Pay with NEAR wallet</ProceedButton>

      {hasSenderWallet && (
        <ProceedButton onClick={() => onSenderWallet(currency || defaultCurrency)}>
          Pay with Sender Wallet
        </ProceedButton>
      )}

      {hasTrustWallet(currency || defaultCurrency) && (
        <ProceedButton onClick={() => onTrustWallet(currency || defaultCurrency)}>Pay with Trust Wallet</ProceedButton>
      )}

      <div className={b("or")}>or</div>

      <div className={b("qr-code-container")}>
        <QRCodeSVG value={`${payment.address}?amount=${payment.displayAmount}`} />
      </div>

      <div className={b("payment-method-text")}>
        Scan following&nbsp;QR code with your wallet or&nbsp;enter Account&nbsp;ID&nbsp;and amount manually.
      </div>

      <a href="http://nearpay.co" target="_blank" rel="noopener noreferrer" className={classNames(b("link-nearpay"))}>
        <span className={b("link-nearpay_gray")}>Powered by</span>
        {"  "}
        <NpLogoIcon className={b("link-nearpay_icon")} width="20" height="15" />
        {"  "}
        <span>NearPay</span>
      </a>
    </WidgetContainer>
  );
};
